export default {
	data(){
		return{
			userInfo:{},
			avatar:'../../static/image/ylc/11.png',
			userName:'未登录',
			phonenumber:'',
			code:'',
			reward:0,
			beanDirect:0,
			beanIndirect:0,
			beanPoint:0
		}
	},
	methods:{
		// 修改个人信息
		handleToEditUserInfo(){
			uni.navigateTo({
				url:'userInfo'
			})
		},
		// 查看消息
		handleToMsg(){
			uni.navigateTo({
				url:'message'
			})
		},
		// 跳转到设置
		handleToSetting(){
			uni.navigateTo({
				url:'setting'
			})
		},
		// 复制邀请码
		copyCode(){
			uni.setClipboardData({
				data:this.code
			})
		},
		// 查看佣金明细
		handleToCommission(){
			uni.navigateTo({
				url:'commission?reward='+this.reward
			})
		},
		// 去提现
		handleToCash(){
			uni.navigateTo({
				url:'../cash/cash?reward='+this.reward
			})
		},
		// 去充值
		handleToRecharge(){
			uni.switchTab({
				url:'../Find/Find'
			})
		},
		// 查看明细
		handleToCheckDetail(){
			uni.navigateTo({
				url:'beanDetail'
			})
		},
		// 我的积分
		handleToIntegral(){
			uni.navigateTo({
				url:'integral?points='+this.beanPoint
			})
		},
		// 我的订单
		handleToMyOrder(){
			uni.navigateTo({
				url:'../myOrder/myOrder'
			})
		},
		// 我的团队
		handleToMyTeam(){
			uni.navigateTo({
				url:'../myTeam/myTeam'
			})
		},
		// 我的收藏
		handleToMyCollection(){
			uni.navigateTo({
				url:'../myCollection/myCollection'
			})
		},
		// 打开签到
		handleToPunch(){
			uni.navigateTo({
				url:'../Punch/Punch?point='+this.beanPoint
			})
		},
		// 联系客服
		handleCallPhone(){
			uni.showModal({
				title:'我的客服',
				content:'客服电话：13888888888',
				success: (res) => {
					if(res.confirm){
						uni.makePhoneCall({
							phoneNumber:'1388888888'
						})
					}
				}
			})
		},
		// 获取用户信息
		async getUserInfo(){
			let res = await this.$fetch(this.$api.getUserInfo,{},'GET')
			console.log(res);
			if(res.data.avatar){
				this.avatar  = res.data.avatar
			}
			this.phonenumber = res.data.phonenumber
			this.userName = res.data.userName
			this.reward = res.data.userInfo.reward
			this.code = res.data.userInfo.code
			this.beanDirect = res.data.userInfo.beanDirect
			this.beanIndirect = res.data.userInfo.beanIndirect
			this.beanPoint = res.data.userInfo.beanPoint
		}
	},
	onShow() {
		this.getUserInfo()
	}
}
